.wfw-tooltip {
  font-size: 11px;
  padding: 5px 10px 6px;
  position: absolute;
  height: max-content;
  display: inline-block;
  white-space: nowrap;
  background-color: $white;
  color: $black;
  font-family: $fontRegular;
  margin: 0;
  z-index: 999999;
  text-align: justify;
  border: 1px solid $porcelain;
  border-radius: 3px;

  &::after {
    content: "";
    border-bottom: 1px solid $porcelain;
    border-right: 1px solid $porcelain;
    background-color: $white;
    position: absolute;
    height: 10px;
    width: 10px;
    bottom: -6px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: block;
    @include rotate(45deg);
  }
}
