#woodpecker-options {
  .settings-container {
    @include displayFlex();
    width: 100%;
    min-height: 750px;
    margin-top: 27px;

    #wfw-privacy_policy {
      margin-bottom: 0;
    }

    .error {
      margin-top: 10px;
      color: #ec1940;
      font-size: 14px;
      display: block;
      padding-left: 2px;
    }

    .info {
      margin-top: 10px;
    }

    .save-container .save-box  {
      left: unset;
      right: unset;
      margin: 0;
  
      &.fixed {
        left: unset;
      }
    }

    &__menu {
      width: 210px;
      height: 280px;
      position: sticky;
      top: 30px;
      margin-right: 100px;

      ul {
        font-size: 14px;
        font-family: $fontRegular;
        padding-left: 38px;

        li.active{
          font-family: $fontBold;
          position: relative;

          &::before{
            content: "";
            width: 17px;
            height: 3px;
            background-color: $green;
            position: absolute;
            left: -38px;
            display: block;
            top: 50%;
            @include transition(width .3s ease);

          }
        }

        li {
          margin-bottom: 44px;
          position: relative;
          cursor: pointer;

          &::before{
            content: "";
            width: 0;
            height: 3px;
            background-color: $green;
            position: absolute;
            left: -38px;
            display: block;
            top: 50%;
            @include transition(width .3s ease);
          }

          &:hover::before{
              content: "";
              width: 17px;
          }
        }
      }
    }

    &__content {
      width: 100%;
      display: block;
      position: relative;
      padding-top: 16px;

      &--label {
        font-size: 14px;
        text-transform: uppercase;
        font-family: $fontBold;
        margin-bottom: 24px;
        display: block;
        width: 100%;
        cursor: default;
      }

      &--scroll {
        border: 1px solid $lighter-grey;
        height: 164px;
        max-width: 670px;
        height: 164px;
        margin-bottom: 56px;
        padding: 23px 10px 23px 23px;
        @include transition(.3s ease);

        &.active {
          border: 1px solid $black;
        }
      }

      &--textarea {
        max-width: 670px;
        width: 100%;
        max-height: 162px;
        resize: none;
        border: 0;
        border-radius: 0;
        line-height: normal;
        padding: 0 13px 0 0;

        &:focus {
          outline: none;
          border: 0;
        }
      }

      .api.error {
        border-bottom: 1px solid #ec1940;
      }
    }
  }
}
