.dropdownWidget {
  position: relative;
  display: inline-block;

  &__error {
    opacity: 0;
    position: absolute;
    color: $red;
    font-family: $fontItalic;
    @include transition(.3s ease);
  }

  &.active {
    .dropdownWidget__error {
      opacity: 1;
    }

    .dropdownWidget__rows {
      border-bottom: 1px solid $red;
    }
  }

  &__rows {
    display: block;
    width: 300px;
    border-bottom: 1px solid #cdcbcb;
    text-align: left;
    height: 18px;
    cursor: pointer;
    position: relative;
    padding-bottom: 6px;
    padding-top: 5px;
    font-size: 14px;
    @include transition(all .3s ease);

    &.new {
      font-family: $fontItalic;
      color: $lighter-grey;
    }

    &::after {
      content: "";
      background-position: center;
      background-repeat: no-repeat;
      @include maskImage("../images/dropdown_up.svg");
      @include maskRepeat(no-repeat);
      background-color: #ef4363;
      position: absolute;
      width: 9px;
      height: 5px;
      right: 0;
      top: 13px;
    }

    &:hover {
      border-bottom: 1px solid $black;
    }

    &:focus {
      border-bottom: 1px solid $black;
    }

    &--block {
      position: absolute;
      box-sizing: border-box;
      padding: 15px 5px 15px 15px;
      font-family: 'Poppins Regular';
      font-size: 14px;
      line-height: 22px;
      color: $black;
      background-color: $white;
      box-shadow: 0 0 3px rgba(189, 189, 189, 0.8);
      top: 29px;
      width: 300px;
      text-align: left;
      z-index: 2;

      &__value, a {
        text-transform: uppercase;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        outline: none;
        cursor: pointer;
        font-size: 14px;
        position: relative;
        display: block;
        padding: 0 0 9px;
        margin: 0 20px 5px 0;
        text-decoration: none;
        color: $black;
        @include transition(.2s ease);

        &:focus {
          outline: none;
          box-shadow: none;
        }

        &:hover {
          text-decoration: none;
          color: $black;

          &::after {
            height: 2px;
            background-color: $green;
          }
        }

        &::after {
          content: "";
          height: 1px;
          width: 100%;
          position: absolute;
          bottom: 0;
          background-color: $lighter-grey;
          display: block;
        }
      }
    }
  }
}
