#woodpecker-options {
  .switch-widget {
    display: inline-block;
    cursor: pointer;
    @include userSelect(none);

    .sw-cont {
      position: relative;
      @include displayFlex();
      @include alignItems(center);

      input[type=checkbox] {
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0;
        top: 2px;
        z-index: 99;
      }
    }

    .sw-label {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-left: 15px;
      min-width: 15px;
      @include flex(1 1 auto);

      .sw-left {
        margin-left: 0;
        margin-right: 15px;
      }
    }

    input[type=checkbox]:checked + .sw-icon {
      div {
        background-color: $green;
        position: absolute;
        left: 30px;
      }
    }

    .sw-icon {
      width: 53px;
      height: 25px;
      border-width: 1px;
      border-style: solid;
      border-color: #bdbdbd;
      padding: 2px;
      @include boxSizing(border-box);
      @include borderRadius(13px);

      div {
        width: 19px;
        height: 19px;
        background-color: #d0d2d3;
        left: 4px;
        position: absolute;
        z-index: 98;
        @include boxSizing(border-box);
        @include borderRadius(50%);
        @include translateX(0);
        @include transition(all .2s ease);
      }
    }
  }
}
