#woodpecker-options {
  .forms-container {
    @include displayFlex();
    width: 100%;
    justify-content: space-between;
    padding-bottom: 75px;

    .dropdownWidget {
      position: relative;

      .wfw-mapping {
        text-transform: lowercase;
  
        &::first-letter {
          text-transform: uppercase;
        }
      }

      .error-text {
        position: absolute;
        bottom: -18px;
        opacity: 0;
        visibility: hidden;
        font-size: 12px;
        color: $red;
        @include transition(all 0.3s ease);
      }

      &.duplicate {
        .wfw-mapping {
          border-color: $red;
        }

        .error-text {
          visibility: visible;
          opacity: 1;
        }
      }
    }


    .form-title {
      font-family: $fontBold;
      font-size: 22px;
      margin-top: 44px;
      margin-bottom: 59px;
    }

    &__tabs {
      font-family: $fontRegular;
      font-size: 14px;
      text-transform: uppercase;
      width: 260px;
      @include displayFlex();
      justify-content: space-between;
      margin-bottom: 73px;

      &--tab {
        position: relative;
        cursor: pointer;

        &.active {
          font-family: $fontBold;

          &::after {
            width: 100%;
          }
        }

        &:hover::after {
            width: 100%;
        }

        &::after {
          content: "";
          width: 0;
          height: 3px;
          position: absolute;
          left: 0;
          right: 0;
          bottom: -10px;
          margin: auto;
          background-color: $blue;
          @include transition(width 0.3s ease-in-out);
        }
      }
    }

    &__panel {
      width: 100%;

      .input-field {
        display: none;
      }

      .draggable {
        font-family: $fontRegular;
        font-size: 12px;
        display: inline-block;
        background-color: $white;
        border-top: 1px solid $blue;
        border-bottom: 1px solid $blue;
        text-transform: uppercase;
        margin-bottom: 12px;
        margin-left: 6px;
        margin-right: 9px;
        padding: 0 6px 0 10px;
        white-space: nowrap;
        max-height: 16px;
        position: relative;
        line-height: 16px;
        vertical-align: baseline;
        color: $blue;
        cursor: pointer;
        @include userSelect(none);

        &.disabled {
          color: $grey;
          border-top: 1px solid $grey;
          border-bottom: 1px solid $grey;
          pointer-events: none;
          cursor: default;

          &::after, &::before {
            background-image: url("../images/field-border-grey.png");
          }
        }

        &::after {
          content: "";
          background-image: url("../images/field-border-blue.png");
          height: 18px;
          width: 10px;
          position: absolute;
          right: -9px;
          top: -1px;
          background-repeat: no-repeat;
          transform: rotate(180deg);
        }

        &::before {
          content: "";
          background-image: url("../images/field-border-blue.png");
          height: 18px; 
          width: 10px;
          left: -6px;
          top: -1px;
          position: absolute;
          background-repeat: no-repeat;
        }
      }

      .form-title {
        margin-bottom: 54px;
      }
    }

    &__preview {
      font-family: 'Poppins', sans-serif;
      font-weight: 300;
      width: 463px;
      min-height: 740px;
      text-align: center;
      padding-top: 76px;

      .draggable, .ui-state-default {
        display: block;
        width: 430px;
        min-width: 430px;
        border: 0;
        text-align: left;
        color: $grey;
        font-size: 12px;
        text-transform: uppercase;
        padding: 13px 15px 23px;
        margin-bottom: 18px;
        border-radius: 10px;
        min-height: 61px;
        cursor: pointer;
        background-color: $white;
        @include transition(box-shadow .3s ease);

        &.ui-sortable-helper, &.active {
          box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
        }
      }

      .input-field {
        display: block;
        height: 42px;
        width: 100%;
        display: block;
        border-bottom: 1px solid $form-grey;
        @include transition(all 0.3s ease-in-out);
      }

      input[type="submit"] {
        margin: auto;
        border-radius: 15px;
        display: block;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 18px;
        line-height: 40px;
        height: 40px;
        width: 276px;
        outline: none;
        padding: unset;
        border: 0;
        @include transition(all .3s ease);

        &:hover {
          text-decoration: none;
        }

        &:focus {
          text-decoration: none;
        }
      }

      .wfw-privacy_policy {
        @include displayFlex();
        justify-content: center;
        margin-top: 24px;
        font-weight: 300;
        font-size: 12px;

        p {
          margin: 0 0 0 5px;
        }

        a {
          text-decoration: underline;
          color: $blue;

          &:hover{
            text-decoration: none
          }
        }
      }
      
      .ui-state-default:hover, .draggable:hover{
        box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
      }
      
      .ui-state-default.hover, .draggable.hover {
         color: $black;

         .input-field {
           border-bottom: 1px solid $black;
         }
      }

      .ui-state-default.duplicate, .draggable.duplicate {
        color: $red;
        
        .input-field {
          border-bottom: 1px solid $red;
        }
      }
    }

    .row {
      @include displayFlex();
      margin-bottom: 24px;

      &.required {
        @include displayFlex();
        margin-top: 50px;
        font-weight: 300;
        font-size: 14px;
        justify-content: flex-start;
        margin-left: 23px;
      }

      .label {
        font-size: 14px;
        text-transform: uppercase;
        margin-right: 14px;
        padding-top: 6px;
        white-space: nowrap;
      }

      .value {
        max-width: 300px;

        &.disabled {
          color: $grey;
        }
      }
    }
    
    .delete-field {
      margin-top: 22px;
    }

    .style-container__content {
      .wfw-switch {
        display: inline-block;
        margin-bottom: 43px;
        position: relative;
        top: 7px;
      }

      &--pickers {
        margin-top: 70px;
        
        &__box {
          margin-bottom: 32px;
          @include displayFlex();
          width: 100%;
          max-width: 670px;
          height: 30px;
          line-height: 30px;
        }

        &__span {
          font-size: 14px;
          text-transform: uppercase;
          margin-right: 14px;
          white-space: nowrap;
        }
      }

      &--checkbox {
         position: absolute;
         right: -73px;
      }

      &--span {
         font-size: 14px;
         text-transform: uppercase;
         margin-right: 14px;
         padding-top: 6px;
         white-space: nowrap;
      }

      &--label {
        font-size: 14px;
        text-transform: uppercase;
        font-family: $fontBold;
        margin-bottom: 24px;
        display: block;
        width: 100%;
      }
    }

    .fields-box__panel {
      width: 635px;
    }

    .fields-settings-box {
      &__panel, &__empty {
        opacity: 0;
        height: 0;
        overflow: hidden;
        @include transition(opacity 0.3s ease-in-out);

        &.active {
          opacity: 1;
          height: auto;
        }
      }
    }

    .fields-box, .fields-settings-box, .style-box {
      opacity: 0;
      height: 0;
      overflow: hidden;
      @include transition(opacity 0.3s ease-in-out);

      &.active {
        opacity: 1;
        height: auto;
      }
    }
  }

  .save-container .save-box{
    left: 0;
    right: 0;
    margin: auto;

    &.fixed {
      left: 160px;
    }
  }
}
