#woodpecker-options {
  .prospect-content {
    display: block;
    width: 100%;

    .tooltip {
      bottom: 22px;
    }

    .tags-panel {
      min-height: 53px;
      margin-top: 12px;
      margin-bottom: 5px;
    }
     .filter-panel {
      span:nth-of-type(3) {
        margin-left: 19px;
      }
     }

    h5 {
      font-family: $fontBold;
      text-transform: uppercase;
      font-size: 20px;
      margin-bottom: 64px;
      margin-top: 44px;
    }

    &__table {
      font-size: 14px;

      &--content {
        @include displayFlex();
      }

      &--email {
        width: 25%;
      }

      &--head {
        font-size: 12px;
        border-top: 1px solid $lighter-grey;
        border-bottom: 1px solid $lighter-grey;
        text-transform: uppercase;
        padding: 9px 15px;
        height: 19px;
      }

      &--scrollable {
        width: 100%;
        overflow-x: auto;
      }

      &--item.title, &--row.title {
        font-size: 12px;
        border-top: 1px solid $lighter-grey;
        text-transform: uppercase;
        padding: 9px 15px;
      }

      &--row.title {
        width: 100%;
        min-width: max-content;
      }

      &--item {
        padding: 25px 15px;
        display: block;
        border-bottom: 1px solid $lighter-grey;

        .row-table {
            white-space: nowrap;
            padding: 0 15px 0 0;
            height: 19px;
            position: relative;

            &__text {
              overflow: hidden;
              text-overflow: ellipsis;
              display: block;
              height: 100%;

              &::before {
                content: '';
                display: block;
              }
            }
        }
      }

      &--row {
        @include displayFlex();
        min-width: max-content;
        border-bottom: 1px solid $lighter-grey;
        padding: 25px 15px;
        width: 100%;
        min-width: max-content;

        .row-table {
          white-space: nowrap;
          padding: 0 25px 0 0;
          height: 19px;
          width: 225px;
          position: relative;

          &__text {
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            height: 100%;

            &::before {
              content: '';
              display: block;
            }
          }
        }
      }
    }
  }
}
