/* fonts */
@font-face {
  font-family: 'Poppins Regular';
  src: url('https://app.woodpecker.co/css/fonts/poppins-regular.eot');
  src: url('https://app.woodpecker.co/css/fonts/poppins-regular.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular.svg#avignonbook') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Poppins Semibold';
  src: url('https://app.woodpecker.co/css/fonts/poppins-semibold.eot');
  src: url('https://app.woodpecker.co/css/fonts/poppins-semibold.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/poppins-semibold.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/poppins-semibold.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/poppins-semibold.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Poppins Regular Italic';
  src: url('https://app.woodpecker.co/css/fonts/poppins-regular-italic.eot');
  src: url('https://app.woodpecker.co/css/fonts/poppins-regular-italic.eot?#iefix') format('embedded-opentype'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular-italic.woff') format('woff'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular-italic.ttf') format('truetype'),
  url('https://app.woodpecker.co/css/fonts/poppins-regular-italic.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bradley';
  src: url('https://app.woodpecker.co/css/fonts/bradley.eot');
  src: url('https://app.woodpecker.co/css/fonts/bradley.eot?#iefix') format('embedded-opentype'),
       url('https://app.woodpecker.co/css/fonts/bradley.woff') format('woff'),
       url('https://app.woodpecker.co/css/fonts/bradley.ttf') format('truetype'),
       url('https://app.woodpecker.co/css/fonts/bradley.svg#avignondemi') format('svg');
  font-weight: normal;
  font-style: normal;
}

@import url('https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap');

/* colors */
$green: #97c33a;
$green-hover: #a5d443;
$grey: #bdbdbd;
$white: #ffffff;
$black: #0b0204;
$blue: #1b88c7;
$blue-hover: #2098dd;
$lighter-grey: #e7e6e6;
$red: #ec1940;
$form-grey: #898989;
$porcelain: #F2F2F2;

$fontRegular: 'Poppins Regular';
$fontBold: 'Poppins Semibold';
$fontItalic: 'Poppins Regular Italic';
$fontBradley: 'Bradley';

.woodpecker-options-wrap {
  margin-top: 20px;
  margin-right: 20px;
  background-color: $black;
  font-family: $fontRegular;
}

.toplevel_page_wfw {
  .wp-menu-image {
    text-align: center !important;
    display: block;
    padding: 0;
    margin: 0;

    img {
      display: block;
      width: auto;
      max-height: 100%;
      margin: 0 auto !important;
      padding: 5px 0 0 0 !important;
      height: 25px;
    }
  }
}

#woodpecker-options {
  a {
    outline: none;

    &:focus {
      box-shadow: unset;
    }
  }

  .save-container {
    display: block;
    height: 116px;
    width: 100%;
  }

  img {
    max-width:100%;
    height: auto;
  }

  .info {
    color: #1b88c7;
    font-size: 12px;
    display: block;
    padding-left: 7px;

    &::before {
      content: "";
      background-image: url('../images/icon_info.svg');
      height: 18px;
      width: 18px;
      background-repeat: no-repeat;
      background-position: center;
      left: -7px;
      position: relative;
      display: inline-block;
      top: 4px;
    }
  }

  h1 {
    color: $white;
    font-weight: normal;
    font-family: $fontBold;
    margin-top: 0;
    padding-top: 0;
    margin-bottom: 0;
    line-height: 1em;
    padding-left: 10px;
  }

  h5 {
    font-size: 14px;
    font-weight: normal;
  }

  .close {
    background-image: url("../images/button_delete_small.svg");
    width: 8px;
    height: 8px;
  }

  .fontDemi{
    font-family: $fontBold;
  }

  .fontBook{
    font-family: $fontRegular;
  }

  .uppercase {
    text-transform: uppercase;
  }

  .hand {
    cursor: pointer;
  }

  .textLink {
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  }

  .linkLabel {
    text-decoration: underline;
    color: #1b88c7;
    font-size: 12px;
    cursor: pointer;
    @include transition(all .3s ease);

    &:hover {
      text-decoration: none;
      color: #2098dd;
    }
  }

  input {
    &::-webkit-input-placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }

    &:-ms-input-placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }

    &::placeholder {
      font-family: $fontItalic;
      color: #d7d7d7;
    }
  }

  input[type="text"], input[type="password"] {
    border-left: 0;
    border-right: 0;
    border-top: 0;
    background-color: $white;
    border-bottom: 1px solid $grey;
    box-shadow: none;
    padding-left: 0;
    padding-right: 0;
    font-size: 14px;
    font-family: $fontRegular;
    border-radius: 0;
    color: $black;
    line-height: normal;
    width: 100%;
    max-width: 670px;
    display: block;
    outline: none;
    @include transition(all .3s ease);

    &:focus {
      border-top: 0;
      border-left: 0;
      border-right: 0;
      outline: none;
      border-bottom: 1px solid $black;
    }
  }

  .button {
    border-color: #d7d7d7;
    color: $black;

    &:focus {
      box-shadow: unset;
    }
  }

  .btn-link {
    font-size: 12px;
    cursor: pointer;
    display: inline-block;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }

    &.blue {
      color: $blue;
      @include transition(all .3s ease);

      &:hover {
        color: $blue-hover;
      }
    }

    &.red {
      color: $red;
      @include transition(all .3s ease);

      &:hover {
        color: $red;
      }
    }
  }

  .btn-woodpecker {
    background-color: $green;
    text-transform: uppercase;
    padding: 0 15px;
    color: $white;
    line-height: 0;
    border-radius: 0;
    border: 0;
    font-size: 14px;
    font-family: $fontRegular;
    width: 107px;
    @include transition(all .3s ease);

    &:hover {
      background-color: $green-hover;
      border: 0;
    }
  }

  textarea {
    width: 100%;
    max-width: 825px;
    resize: none;
    border: 1px solid $grey;
    box-shadow: none;
    font-size: 14px;
    padding: 23px;
    min-height: 86px;
    @include transition(all .3s ease);

    &:focus {
      border: 1px solid $black;
    }
  }

  .col-container {
    background-color: $white;
    border: 1px solid #ccc;
    padding: 50px 20px 20px 20px;

    &__margin {
      margin: auto;
      max-width: 1366px;
      position: relative;
    }
  }

  .wfw-checkbox {
    @include userSelect(none);
    position: relative;

    .custom-checkbox {
      position: absolute;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      left: -23px;
      display: block;
      height: 15px;
      width: 15px;
      border: 1px solid #e1e1e1;
      border-radius: 2px;
      z-index: 98;
      @include transition(all .3s ease);

      &::after {
        content: "";
        position: absolute;
        opacity: 0;
        @include maskImage('../images/tick.svg');
        @include maskRepeat(no-repeat);
        background-color: $green;
        height: 9px;
        width: 9px;
        top: 1px;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        @include transition(all .3s ease);
      }
    }

    input[type="checkbox"]:checked ~ .custom-checkbox::after {
      opacity: 1;
    }

    input[type="checkbox"] {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 17px;
      width: 17px;
      top: 0;
      bottom: 0;
      margin: auto;
      left: -23px;
      z-index: 99;
    }
  }

  .disabled {
    color: $grey;

    input[type="checkbox"] {
      cursor: default;
    }

    .custom-checkbox::after {
      background-color: $grey;
    }
  }
}

.scrollable {
  position: relative;

  &:focus {
    outline: 0;
  }

  .viewport {
    position: relative;
    overflow: hidden;
    padding-bottom: 10px;
  }

  .overview {
    position: absolute;
  }

  .scroll-bar {
    display: none;

    &.vertical {
      position: absolute;
      right: 0;
      height: 100%;
    }

    &.horizontal {
      position: relative;
      width: 100%;
    }

    .thumb {
      position: absolute;
    }
  }
}

.scrollable .scroll-bar.vertical .thumb {
  width: 100%;
  min-height: 10px;
}

.scrollable .scroll-bar.horizontal .thumb {
  height: 100%;
  min-width: 10px;
  left: 0;
}

.not-selectable {
  @include userSelect(none);
}

.scrollable.default-skin {
  padding-right: 10px;
  padding-bottom: 6px;
}

.scrollable.default-skin .scroll-bar.vertical {
  width: 6px;
}

.scrollable.default-skin .scroll-bar.horizontal {
  height: 6px;
}

.scrollable.default-skin .scroll-bar .thumb {
  background-color: $lighter-grey;
  @include borderRadius(4px);
}

.scrollable.default-skin .scroll-bar:hover .thumb {
  background-color: #d0d2d3;
}