#woodpecker-options {
  .shortcodes-content {
    display: block;
    width: 100%;

    h5 {
      font-family: $fontRegular;
      margin-bottom: 48px;
      margin-top: 44px;
      line-height: 23px;
    }

    .title {
      font-size: 14px;
      text-transform: uppercase;
      font-family: $fontBold;
    }

    .code {
      font-size: 14px;
      font-family: Arial, sans-serif;
      margin-top: 23px;
      margin-bottom: 53px;
      position: relative;
      display: inline-block;

      .copied-text {
        left: 0;
        top: 6px;
        position: absolute;
        padding-left: 5px;
      }

      .code-copy {
        width: 23px;
        height: 23px;
        cursor: pointer;
        position: relative;
        top: 7px;
        margin-left: 16px;
        display: inline-block;

        &::after {
          content: "";
          border: 1px solid #bcbcbc;
          width: 14px;
          height: 14px;
          position: absolute;
          bottom: 1px;
          right: 1px;
          background-color: $white;
        }

        &::before {
          content: "";
          border: 1px solid #bcbcbc;
          width: 14px;
          height: 14px;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }

    .filter-panel {
      margin-top: 59px;
    }

    .copied-text {
      position: absolute;
      background-color: $white;
      color: $green;
      width: 100%;
      height: 100%;
      top: 18px;
      padding-left: 40px;
      font-family: $fontRegular;

      &:before {
        content: "";
        height: 12px;
        width: 12px;
        position: relative;
        display: inline-block;
        left: -5px;
        background-image: url("../images/tick_small.svg");
      }
    }

    &__table {
      font-size: 14px;

      &--row.title {
        font-size: 12px;
        border-top: 1px solid $lighter-grey;
        text-transform: uppercase;
        padding: 10px 15px;
      }

      &--row {
        display: grid;
        grid-template-columns: 60px minmax(100px, 1fr) minmax(100px, 1fr) 308px 35px;
        border-bottom: 1px solid $lighter-grey;
        padding: 25px 15px;

        &__status {
          text-align: center;
        }

        &__natural {
          padding-left: 35px;
        }

        &__content {

          &--row{
            display: grid;
            grid-template-columns: 60px minmax(100px, 1fr) minmax(100px, 1fr) 308px 35px;
            border-bottom: 1px solid $lighter-grey;
            padding: 25px 15px;
            height: 55px;

            .row-table {
                white-space: nowrap;
                padding: 0;
                position: relative;
                height: 19px;

                &__text {
                  padding-left: 35px;
                  margin-top: 18px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: block;
                  height: 100%;

                  &::before {
                    content: '';
                    display: block;
                  }
                }

                &__text.arial {
                  font-family: Arial, sans-serif;
                }

                &__code {
                  width: 23px;
                  height: 23px;
                  cursor: pointer;
                  display: block;
                  margin-top: 18px;
                  margin-left: auto;
                  margin-right: auto;
                  position: relative;

                  &::after {
                    content: "";
                    border: 1px solid #bcbcbc;
                    width: 14px;
                    height: 14px;
                    position: absolute;
                    bottom: 1px;
                    right: 1px;
                    background-color: $white;
                  }

                  &::before {
                    content: "";
                    border: 1px solid #bcbcbc;
                    width: 14px;
                    height: 14px;
                    position: absolute;
                    top: 0;
                    left: 0;
                  }
                }

                .icon {
                  text-align: center;
                  display: block;
                  width: 100%;

                  &_completed {
                    display: block;
                    background-image: url("../images/icon_completed.svg");
                    width: 27px;
                    height: 20px;
                    margin: auto;
                    background-size: cover;
                    margin-top: 7px;
                    background-repeat: no-repeat;
                  }

                  &_draft {
                    display: block;
                    background-image: url("../images/icon_draft.svg");
                    width: 15px;
                    height: 21px;
                    margin: auto;
                    background-size: contain;
                    margin-top: 6px;
                    background-repeat: no-repeat;
                  }

                  &_edited {
                    display: block;
                    background-image: url("../images/icon_edited.svg");
                    width: 16px;
                    height: 24px;
                    margin: auto;
                    background-size: cover;
                    margin-top: 5px;
                    background-repeat: no-repeat;
                  }

                  &_stopped {
                    display: block;
                    background-image: url("../images/icon_stopped.svg");
                    width: 19px;
                    height: 19px;
                    margin: auto;
                    background-size: cover;
                    margin-top: 7px;
                    background-repeat: no-repeat;
                  }

                  &_running {
                    display: block;
                    background-image: url("../images/icon_running-sprite.svg");
                    width: 17px;
                    height: 20px;
                    margin: auto;
                    background-size: cover;
                    background-repeat: no-repeat;
                    margin-top: 7px;
                  }

                  &_paused {
                    display: block;
                    background-image: url("../images/icon_paused.svg");
                    width: 18px;
                    height: 20px;
                    margin: auto;
                    background-size: cover;
                    background-repeat: no-repeat;
                    margin-top: 7px;
                  }

                  &_text {
                    display: block;
                    font-size: 10px;
                    margin-top: 8px;
                  }
              }
            }
          }
        }
      }
    }
  }
}
