#woodpecker-options {
  .save-box.active {
    opacity: 1;
    height: 29px;
    background-color: $white;
    overflow: hidden;
    padding: 43px 10px;
  }

  .save-box.fixed {
    position: fixed;
    z-index: 999;
  }

  .save-box {
    width: 100%;
    max-width: 843px;
    border-top: 1px solid $lighter-grey;
    opacity: 0;
    height: 0;
    overflow: hidden;
    position: absolute;
    bottom: 0;
    @include transition(all .3s ease, left 0s);

    &__info {
      position: absolute;
      text-align: center;
      width: 100%;
      font-size: 14px;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      min-height: 0;
      max-height: 0;
      opacity: 0;
      @include transition(opacity .3s ease);

      &.show {
        opacity: 1;
        min-height: 18px;
        max-height: 18px;
      }
    }

    &__close {
      background-image: url('../images/button_close.svg');
      float: right;
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
      background-position: center;
      margin-top: 7px;
      cursor: pointer;
      opacity: 1;
      @include transition(opacity .3s ease);

      &.hide {
        opacity: 0;
      }
    }

    .save-option {
      opacity: 1;

      &.hide {
        opacity: 0;
      }
    }

    .submit {
      display: inline-block;
      padding: 0;
      margin: 0;
    }
  }
}
