#woodpecker-options {
  .pagination-section {
    font-size: 12px;
    text-align: center;
    padding: 20px 0 80px 0;
    visibility: hidden;

    &__dropdown {
      position: relative;
      display: inline-block;
    }

    input {
        width: 50px;
        font-size: 12px;
        padding: 0;
        text-align: center;
        -webkit-appearance: none;
        margin: 0;
        height: 21px;
        max-width: unset;
        display: inline-block;
        min-height: 21px;

        &:hover {
          border-bottom: 1px solid $black;
        }

        &:focus {
          outline: none;
          border-bottom: 1px solid $black;
        }
    }

    &__max-pages {
      display: inline-block;
      width: 110px;
      text-align: left;
    }

    &__direct {
      display: inline-block;
      margin-left: 43px;

      a {
        color: $black;
        text-decoration: none;

        &:hover {
          color: $blue-hover;
          text-decoration: none;
        }
      }

      .first {
        display: inline-block;
        margin-right: 6px;
        cursor: pointer;
      }

      .last {
        display: inline-block;
        margin-left: 6px;
        cursor: pointer;
      }

      .first.disabled, .last.disabled {
        cursor: default;
        color: #bdbdbd;
        pointer-events: none;
      }

      .prev.disabled, .next.disabled {
        pointer-events: none;

        &::before {
          border-color: #bdbdbd;
        }
        &:hover {
          &:before {
            border-color: #bdbdbd;
          }
        }
      }

      .page-sep {
        display: inline-block;
        background-color: $grey;
        width: 1px;
        height: 14px;
        margin-right: 3px;
        margin-left: 3px;
        top: 3px;
        position: relative;
      }

      .prev, .next {
        margin-top: 2px;
        padding: 3px 6px;
        cursor: pointer;
      }

      .prev {
        margin-right: 2px;
        display: inline-block;
        vertical-align: top;
        width: 5px;
        height: 10px;

        &:hover {
          &::before {
            border-color: $blue-hover;
          }
        }

        &::before {
          display: block;
          content: '';
          width: 7px;
          height: 7px;
          @include boxSizing(border-box);
          border-width: 1px 0 0 1px;
          border-style: solid;
          border-color: $black;
          @include transformOrigin(left top);
          @include transform(translateY(5px) rotate(-45deg));
        }
      }

      .next {
        margin-left: 9px;
        display: inline-block;
        vertical-align: top;
        width: 5px;
        height: 10px;

        &:hover {
          &::before {
            border-color: $blue-hover;
          }
        }

        &::before {
          display: block;
          content: '';
          width: 7px;
          height: 7px;
          @include boxSizing(border-box);
          border-width: 1px 0 0 1px;
          border-style: solid;
          border-color: $black;
          @include transformOrigin(left top);
          @include transform(translateX(5px) translateY(5px) rotate(135deg));
        }
      }

      &--current-pages{
        display: inline-block;
        text-align: center;
        min-width: 110px;
        color: $black;
      }
    }

    &__rows {
      display: block;
      width: 47px;
      margin-left: 9px;
      margin-right: 50px;
      border-bottom: 1px solid #cdcbcb;
      text-align: left;
      height: 15px;
      cursor: pointer;
      position: relative;
      padding-bottom: 4px;
      @include transition(all .3s ease);

      &::after {
        content: "";
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../images/dropdown_up.svg");
        position: absolute;
        width: 9px;
        height: 5px;
        right: 0;
        top: 6px;
      }

      &:hover {
        border-bottom: 1px solid $black;
      }

      &:focus {
        border-bottom: 1px solid $black;
      }

      &--block {
        position: absolute;
        box-sizing: border-box;
        padding: 15px;
        font-family: 'Poppins Regular';
        font-size: 14px;
        line-height: 22px;
        color: $black;
        background-color: $white;
        box-shadow: 0 0 3px rgba(189, 189, 189, 0.8);
        bottom: 25px;
        width: 70px;
        text-align: left;

        &__value, a {
          text-transform: uppercase;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          outline: none;
          cursor: pointer;
          font-size: 14px;
          position: relative;
          display: block;
          padding: 0 0 9px;
          margin: 0 0 5px;
          text-decoration: none;
          color: $black;
          @include transition(.2s ease);

          &:focus {
            outline: none;
            box-shadow: none;
          }

          &:hover {
            text-decoration: none;
            color: $black;

            &:after {
              height: 2px;
              background-color: $green;
            }
          }

          &:after {
            content: "";
            height: 1px;
            width: 100%;
            position: absolute;
            bottom: 0;
            background-color: $lighter-grey;
            display: block;
          }
        }
      }
    }
  }
}
