#woodpecker-options {
  .filter-panel {
    @include displayFlex();

    input {
      font-family: $fontRegular;
      border-top: 0;
      border-left: 0;
      border-right: 0;
      border-bottom: 1px solid #cdcbcc;
      padding: 0 0 5px;
      width: 359px;
      color: $black;
      margin-top: -3px;

      &::placeholder {
        font-family: $fontItalic;
        color: #bdbdbd;
      }

      &:focus, &:hover {
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-bottom: 1px solid $black;
        outline: none;
      }
    }

    span {
      font-size: 14px;
      margin-left: 15px;

      &:nth-of-type(3) {
        margin-left: 25px;
      }
    }

    span.linkLabel {
      font-size: 12px;
    }
  }

  .tags-panel {
    margin-top: 17px;
    margin-bottom: 10px;
    min-height: 76px;
    flex-wrap: wrap;
    @include displayFlex();

    &__tag {
      max-width: calc(50% - 10px);
      height: 28px;
      font-family: $fontRegular;
      font-size: 12px;
      color: $white;
      background-color: #bdbdbd;
      border-radius: 16px;
      padding: 0 10px;
      margin: 10px 10px 0 0;
      max-width: calc(50% - 10px);
      align-items: center;
      @include displayFlex();

      a {
        span {
          margin-right: 0;
        }
      }

      a, span {
        margin-right: 8px;
        outline: none;

        &:focus {
          outline: none;
          box-shadow: none;
        }
      }

      &--text {
        text-transform: uppercase;
        padding-right: 3px;
      }

      &--status {
        text-transform: lowercase;
        margin-right: 3px;
      }

      &--close {
        margin-top: 2px;
        margin-right: 0;
        display: block;
        cursor: pointer;
      }
    }
  }
}
