#woodpecker-options {
  .how-it-works-content {
    display: block;
    width: 100%;
    max-width: 1000px;
    font-size: 14px;
    line-height: 27px;
    margin-bottom: 80px;

    .intro-block {
      width: 100%;
      max-width: 857px;
      margin-top: 41px;

      h3 {
        margin-top: 71px;
        margin-bottom: 21px;
      }

      &__p {
        margin: 20px 0 40px 0;
      }
    }

    .set-up-block {
      width: 100%;
      max-width: 800px;
    }

    img {
      margin-top: 20px;
      margin-bottom: 54px;
      box-shadow: 0 0 4px rgba(205, 203, 204, 0.75);
    }

    p {
      font-size: 14px;
    }

    ul {
      counter-reset: iterator;
      padding-bottom: 8px;
      margin-bottom: 0;

      li {
        counter-increment: iterator;
        position: relative;
        padding-left: 57px;
        margin-bottom: 11px;
        width: 100%;

        .text-container {
          padding: 0;
          margin: 12px 0 0 0;
          max-width: 875px;
        }

        img {
          position: relative;
          left: -58px;
          margin-top: 30px;
          margin-bottom: 48px;
        }

        &::before {
          content: "";
          position: absolute;
          left: 0;
          background-image: url('../images/red_arrow.png');
          width: 19px;
          height: 12px;
          top: 7px;
        }

        &:after {
          content:  counter(iterator);
          left: 28px;
          color: #ec1940;
          position: absolute;
          top: 0;
        }
      }
    }

    .standard {
      padding-left: 0;

      li {
        margin-bottom: 0;
        padding-left: 20px;

        &::before {
          content: "\2022";
          position: absolute;
          left: 0;
          background-image: none;
          top: 1px;
        }

        &::after {
          content: "";
        }
      }
    }

    a {
      color: #1b88c7;
      text-decoration: underline;

      &:hover {
        color: #2098dd;
        text-decoration: none;
      }
    }

    &__block {
      font-size: 14px;
      font-family: $fontRegular;

      strong {
        font-family: $fontBold;
      }
    }

    h3 {
      font-family: $fontBold;
      text-transform: uppercase;
      font-size: 22px;
      margin-top: 0;
      margin-bottom: 21px;
    }
  }
}
