.popupTooltip {
  box-shadow: 0 0 3px rgba(189, 189, 189, 0.8);
  padding: 14px 9px 25px 26px;
  font-size: 14px;
  line-height: 18px;
  @include boxSizing(border-box);
  background-color: $white;
  display: none;
  position: absolute;
  z-index: 99;

  a {
    color: $black;
    text-decoration: none;

    &:hover{
      color: $black;
      text-decoration: none;
    }
  }

  &__arrow {
    top: -18px;
    display: block;
    position: absolute;
    overflow: hidden;
    width: 32px;
    height: 18px;
    -webkit-transform-origin: 50% 100%;
    -moz-transform-origin: 50% 100%;
    -o-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    @include rotate(0deg);


    .arrow-l, .arrow-r {
      @include boxSizing(border-box);
      width: 16px;
      height: 14px;
      overflow: hidden;
      position: absolute;
      bottom: 0;
      padding-top: 3px;
    }

    .arrow-l {
      left: 0;

      &::before{
        content: '';
        display: block;
        position: absolute;
        @include boxSizing(border-box);
        width: 30px;
        height: 30px;
        box-shadow: 0 0 3px rgba(189, 189, 189, 0.8);
        background-color: $white;
        left: 100%;
        -webkit-transform-origin: 0 0;
        -moz-transform-origin: 0 0;
        -o-transform-origin: 0 0;
        transform-origin: 0 0;
        @include rotate(55deg);
      }
    }

    .arrow-r {
      right: 0;

      &::before {
        content: '';
        display: block;
        position: absolute;
        @include boxSizing(border-box);
        width: 30px;
        height: 30px;
        box-shadow: 0 0 3px rgba(189, 189, 189, 0.8);
        background-color: $white;
        right: 100%;
        -webkit-transform-origin: 100% 0;
        -moz-transform-origin: 100% 0;
        -o-transform-origin: 100% 0;
        transform-origin: 100% 0;
        @include rotate(-55deg);
      }
    }
  }

  &__content {
    max-width: 310px;
    max-height: 412px;
    overflow-y: auto;
    position: relative;

    .empty {
      text-align: center;
      color: $grey;
      font-family: $fontItalic;
      padding-right: 18px;
      margin-top: 11px;
    }

    &--item {
      text-transform: uppercase;
      outline: none;
      cursor: pointer;
      line-height: 18px;
      position: relative;
      display: block;
      padding: 10px 0 10px;
      margin-right: 20px;
      @include transition(.2s ease);

      &:focus {
        outline: none;
        box-shadow: none;
      }

      &:hover {
        &:after {
          height: 2px;
          background-color: $green;
        }
      }

      &:after {
        content: "";
        height: 1px;
        width: 100%;
        position: absolute;
        bottom: 0;
        background-color: $lighter-grey;
        display: block;
      }

      &__text {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &::before {
          content: '';
          display: block;
        }
      }
    }

    &--item-disabled {
      text-transform: uppercase;
      outline: none;
      cursor: default;
      line-height: 22px;
      position: relative;
      display: block;
      padding: 15px 0 10px;
      margin-right: 20px;
      @include transition(.2s ease);

      &:focus {
        outline: none;
        box-shadow: none;
      }

      &:after {
        content: "";
        height: 1px;
        width: 100%;
        position: absolute;
        bottom: 0;
        background-color: $lighter-grey;
        display: block;
      }

      &__text {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &::before {
          content: '';
          display: block;
        }
      }
    }
  }
}
