#woodpecker-options {
  .no-elements {
    font-family: $fontRegular;
    padding: 200px 0;
    text-align: center;

    &__text {
      font-size: 22px;
      line-height: 31px;
      text-transform: uppercase;

      &--small {
        font-size: 16px;
        font-family: $fontItalic;
        text-transform: none;
      }

      a {
        text-transform: none;
        font-size: 16px;
        text-decoration: underline;
        color: #444;
        @include transition(.3s ease);

        &:focus {
          outline: none;
          box-shadow: none;
        }

        &:hover {
          color: #444;
          text-decoration: none;
        }
      }
    }
  }
}
