#woodpecker-options {
  .empty-filter-panel {
    max-height: 0;
    overflow: hidden;
    @include transition(all .4s ease);
    @include scale(0);

    a {
        color: #444;
        text-decoration: underline;
        @include userSelect(none);

        &:hover {
          text-decoration: none;
          color: #444;
        }
    }

    &__img {
      background-image: url("../images/img_empty_filter.gif");
      background-repeat: no-repeat;
      width: 324px;
      height: 251px;
      max-height: 251px;
      background-size: contain;
      background-position: center;
      margin: 0 auto 31px auto;
      @include scale(1);
    }

    &__empty {
      text-align: center;
      text-transform: uppercase;
      font-size: 20px;
    }

    &__btn {
      margin-top: 20px;
      text-transform: none;
      text-decoration: underline;
      font-size: 16px;
      height: 25px;
      cursor: pointer;
      display: inline-block;

      &:hover {
        text-decoration: none;
      }

      &::before {
        content: "";
        position: relative;
        text-decoration: none;
        margin-right: 14px;
        background-image: url("../images/button_delete.svg");
        height: 18px;
        width: 18px;
        background-repeat: no-repeat;
        background-position: center;
        top: 4px;
        display: inline-block;
      }
    }
  }

  .show.empty-filter-panel {
    max-height: max-content;
    padding: 20px 0 110px 0;
    height: 358px;
    overflow: visible;
    @include scale(1);
  }
}
