@mixin maskImage($value) {
  -webkit-mask-image: url($value); 
  mask-image: url($value);
}
@mixin maskRepeat($value) {
  -webkit-mask-repeat: $value;
  -o-mask-repeat: $value;
  -moz-mask-repeat: $value;
  mask-repeat: $value;
}

@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

@mixin mask($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

@mixin translate($x, $y) {
  -webkit-transform: translate($x, $y);
  -moz-transform: translate($x, $y);
  -ms-transform: translate($x, $y);
  -o-transform: translate($x, $y);
  transform: translate($x, $y);
}

@mixin scale($args...) {
  -webkit-transform: scale($args);
  -moz-transform: scale($args);
  -ms-transform: scale($args);
  -o-transform: scale($args);
  transform: scale($args);
}

@mixin rotate($val) {
  -webkit-transform: rotate($val);
  -moz-transform: rotate($val);
  -ms-transform: rotate($val);
  -o-transform: rotate($val);
  transform: rotate($val);
}

@mixin translateX($val) {
  -webkit-transform: translateX($val);
  -moz-transform: translateX($val);
  -ms-transform: translateX($val);
  -o-transform: translateX($val);
  transform: translateX($val);
}

@mixin translateY($val) {
  -webkit-transform: translateY($val);
  -moz-transform: translateY($val);
  -ms-transform: translateY($val);
  -o-transform: translateY($val);
  transform: translateY($val);
}

@mixin borderRadius($val) {
  -webkit-border-radius: $val;
  -moz-border-radius: $val;
  border-radius: $val;
}

@mixin boxSizing($val) {
  -moz-box-sizing: $val;
  -webkit-box-sizing: $val;
  box-sizing: $val;
}

@mixin alignItems($val) {
  -webkit-align-items: $val;
  -moz-align-items: $val;
  -ms-align-items: $val;
  align-items: $val;
}

@mixin displayFlex() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

@mixin flex($args...) {
  -webkit-box-flex: $args;
  -moz-box-flex: $args;
  -webkit-flex: $args;
  -ms-flex: $args;
  flex: $args;
}

@mixin userSelect($val) {
  -webkit-touch-callout: $val;
  -moz-user-select: $val;
  -webkit-user-select: $val;
  -ms-user-select: $val;
  -o-user-select: $val;
  user-select: $val;
}

@mixin transformOrigin($args...) {
  -webkit-transform-origin: $args;
  -moz-transform-origin: $args;
  -o-transform-origin: $args;
  transform-origin: $args;
}


@mixin transform($args...) {
  -webkit-transform: $args;
  -moz-transform: $args;
  -ms-transform: $args;
  -o-transform: $args;
  transform: $args;
}