#woodpecker-options {
  .info-panel {
    position: relative;
    background-color: $black;
    border-bottom: 1px solid rgba(85, 86, 88, 0.4);
    height: 79px;

    &__container {
      margin: auto;
      max-width: 1366px;
      width: 98%;
      display: flex;
      color: $grey;
      font-size: 19px;
      padding-top: 29px;
      justify-content: center;

      &--text {
        font-family: $fontBradley;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      &--arrow {
        background-image: url("../images/arrow-small-grey.svg");
        background-repeat: no-repeat;
        width: 100px;
        height: 22px;
        margin-left: 21px;
        margin-right: 17px;
        margin-top: -3px;
        @include flex(0 0 auto);
      }

      a {
        text-transform: uppercase;
        background-color: $green;
        color: $white;
        font-family: $fontRegular;
        text-align: center;
        font-size: 16px;
        padding: 4px 38px 0;
        height: 23px;
        text-decoration: none;
        margin-top: -4px;
        white-space: nowrap;
        @include transition(all .3s ease);
        
        &:hover {
          text-decoration: none;
          background-color: $green-hover;
        }
      }
    }
  }
}
